.header {
  max-width: 1600px;
  height: 60px;
  background-color: rgba(0, 0, 0, .56);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin: 0 auto;
  padding-top: 18px;
}

.header__container {
  position: relative;
}

.header__wraper {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 34px;
  display: flex;
  position: relative;
}

.logo__site {
  color: #fff;
  fill: #fff;
  align-items: center;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.logo__site:hover, .logo__site:focus {
  transform: scale(1.25);
}

.logo__site:hover .logo__name {
  color: #ff6b08;
}

.logo__name {
  display: none;
}

.navigation-list {
  flex-wrap: wrap;
  flex-grow: 0;
  display: flex;
}

.navigation-list__link {
  text-transform: uppercase;
  color: #fff;
  line-height: 1.667;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.navigation-list__link:hover {
  color: #ff6b08;
  cursor: pointer;
  outline: none;
}

.navigation-list__link-current:after {
  content: "";
  width: 100%;
  height: 3px;
  background-color: #ff6b08;
  display: block;
  bottom: -3px;
}

.navigation-list__item {
  font-weight: 500;
  line-height: 1.17px;
}

.navigation-list__item:not(:last-child) {
  margin-right: 32px;
}

@media screen and (min-width: 768px) {
  .logo__name {
    color: #fff;
    font-size: 30px;
    font-weight: 500;
    line-height: 1.17;
